.layout {
  min-height: 100%;
}
.layout .logo {
  width: 120px;
  height: 31px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px 24px 16px 0;
  float: left;
  display: flex;
  justify-content: center;
  align-items: center;
}
.layout .logo span {
  color: #fff;
  margin-left: 6px;
}
.layout .ant-layout-header {
  height: 128px;
}
.layout .ant-layout-header .title {
  height: 64px;
  font-size: 36px;
  color: #fff;
  text-align: center;
  padding: 10px 0;
}
.layout .ant-layout-header .title span {
  font-size: 48px;
  font-weight: 800;
  margin: 0 10px 0 0;
}
.layout .ant-layout-footer {
  text-align: center;
  bottom: 0;
  width: 100%;
}
